import { BriefReportCompanyReport } from "@rp/react/tanstack-api/brief"

const getBriefReportSectionTitle = (companyData: BriefReportCompanyReport): string =>
  companyData.entityInfo.ticker
    ? `${companyData.entityInfo.name} (${companyData.entityInfo.ticker})`
    : companyData.entityInfo.name

export { getBriefReportSectionTitle }
